.App {
  text-align: start;
 overflow: hidden  !important;
 height: 100%;
}
.subContainer{
  padding-left: 30px;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.topContainer{
  display: flex;
  flex-direction: row;
  flex: 1;
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.image-class {
  width: 100%;
  height: 100%;
}
.App-link {
  color: #61dafb;
}
.image-text {
    position: absolute;
    color: white;
    font-size: 30px;
    padding-left: 20px;
    letter-spacing: 1px;
    font-weight: 600;
    top: 290px;
}
.image-Container {
  flex: 1;
  position: 'relative';
  margin-right: '20px';
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
  
}
